
















import Vue from "vue";
import TreeView from "@/components/TreeView.vue";

export default Vue.extend({
	components: {
		TreeView
	},
	data() {
		return {
			search: "",
			list: [
				{
					name: "test",
					children: [
						{
							name: "test2"
						},
						{
							name: "test3",
							children: [
								{
									name: "test4"
								},
								{
									name: "test5"
								},
								{
									name: "test6"
								},
								{
									name: "test7"
								},
								{
									name: "test8"
								},
								{
									name: "test9"
								},
								{
									name: "test10"
								},
								{
									name: "test11"
								},
								{
									name: "test12"
								},
								{
									name: "test13"
								},
								{
									name: "test14"
								}
							]
						}
					]
				},
				{ name: "RG" }
			]
		};
	},
	computed: {
		getComputedList(): any {
			if (this.search.length != 0) {
				var result: any[] = JSON.parse(JSON.stringify(this.list));
				var findName = (item: any) => {
					if (item.hasOwnProperty("children")) {
						var chk = false;
						item.children = item.children.filter((y: any) => {
							let value = findName(y);
							if (!chk) chk = value;
							return value;
						});
						return chk;
					} else {
						if (item.name.indexOf(this.search) != -1) {
							return true;
						} else {
							return false;
						}
					}
				};
				return result.filter((x: any) => {
					return findName(x);
				});
			} else return this.list;
		}
	}
});
